/*------------------------------------ #RESET -------------------------------------*/
/** As well as using normalize.css, it is often advantageous to remove all margins from certain elements. */
body, h1, h2, h3, h4, h5, h6, p, blockquote, pre, dl, dd, ol, ul, form, fieldset, legend, figure, table, th, td, caption, hr { margin: 0; padding: 0; }

abbr[title], dfn[title] { cursor: help; }

u, ins { text-decoration: none; }

ins { border-bottom: 1px solid; }

ul, ol, dl { list-style: none; margin: 0; padding: 0; }

a { color: inherit; text-decoration: none; }

button { background: none; border: none; cursor: pointer; padding: 0; text-align: left; }

/*------------------------------------ #BOX-SIZING -------------------------------------*/
/** Set the global `box-sizing` state to `border-box`. css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice paulirish.com/2012/box-sizing-border-box-ftw */
html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

/*------------------------------------ #SHARED -------------------------------------*/
/** Where `margin-bottom` is concerned, this value will be the same as the base line-height. This allows us to keep a consistent vertical rhythm. As per: csswizardry.com/2012/06/single-direction-margin-declarations */
h1, h2, h3, h4, h5, h6, blockquote, p, address, table, fieldset, figure, pre { margin-bottom: 20px; }

img { display: block; flex: none; height: auto; max-width: 100%; vertical-align: baseline; }

/* Slider */
.slick-slider { position: relative; display: block; box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }

.slick-list:focus { outline: none; }

.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-track, .slick-slider .slick-list { -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: translate3d(0, 0, 0); -o-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; }

.slick-track:before, .slick-track:after { content: ""; display: table; }

.slick-track:after { clear: both; }

.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; display: none; }

[dir="rtl"] .slick-slide { float: right; }

.slick-slide img { display: block; }

.slick-slide.slick-loading img { display: none; }

.slick-slide.dragging img { pointer-events: none; }

.slick-initialized .slick-slide { display: block; }

.slick-loading .slick-slide { visibility: hidden; }

.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

.slick-arrow.slick-hidden { display: none; }

/*------------------------------------ #FORMS -------------------------------------*/
/**  Global Reset Styles */
input, textarea, select, option, optgroup, button, legend, fieldset { color: #000000; display: block; font-family: inherit; font-size: 16px; font-size: 1rem; line-height: 1.875; }

@media screen and (min-width: 768px) { input, textarea, select, option, optgroup, button, legend, fieldset { font-size: 16px; font-size: 1rem; line-height: 1.875; } }

datalist { font-family: inherit; font-size: 16px; font-size: 1rem; line-height: 1.875; }

@media screen and (min-width: 768px) { datalist { font-size: 16px; font-size: 1rem; line-height: 1.875; } }

label { display: block; position: relative; }

/**  Input & Textarea */
input[type="text"], input[type="email"], input[type="password"], input[type="search"], input[type="color"], input[type="date"], input[type="datetime-local"], input[type="month"], input[type="number"], input[type="tel"], input[type="time"], input[type="url"], input[type="week"], input[list], input[type="file"], select, textarea { background-color: #fff; border: 1px solid #eae5e3; border-radius: 2px; padding: 0.9375rem; width: 100%; }

input[type="text"], input[type="email"], input[type="password"], input[type="search"], input[type="color"], input[type="date"], input[type="datetime-local"], input[type="month"], input[type="number"], input[type="tel"], input[type="time"], input[type="url"], input[type="week"], input[list] { height: 50px; -webkit-appearance: none; }

textarea { overflow: auto; -webkit-appearance: none; }

input[type="range"] { height: 50px; width: 100%; }

input[type="file"] { min-height: 50px; }

input[type="search"] { height: 50px; -webkit-appearance: none; }

input[type="search"]::-webkit-search-cancel-button, input[type="search"]::-webkit-search-decoration { -webkit-appearance: none; }

input[type="checkbox"], input[type="radio"] { display: inline-block; vertical-align: middle; }

select { height: 50px; -webkit-appearance: none; -moz-appearance: none; -ms-appearance: none; -o-appearance: none; line-height: 1.15; }

select::-ms-expand { display: none; }

select[multiple] { height: auto; min-height: 50px; padding: 0; }

select[multiple] option { margin: 0; padding: 0.9375rem; }

fieldset { border: 0; padding: 0; }

legend { font-weight: inherit; padding: 0; }

::-webkit-input-placeholder { color: #000000; }

::-moz-placeholder { color: #000000; }

:-ms-input-placeholder { color: #000000; }

input[disabled], textarea[disabled], select[disabled], option[disabled], button[disabled] { cursor: not-allowed; }

input:focus, textarea:focus { background-color: #fbfbfb; border: 1px solid #d4cac6; }

input[aria-invalid="true"], textarea[aria-invalid="true"], select[aria-invalid="true"] { border: 2px solid darkred; }

/**  Restyle default here */
/*------------------------------------ #HEADINGS -------------------------------------*/
h1 { font-size: 39px; font-size: 2.4375rem; line-height: 1.4em; }

h2 { font-size: 31px; font-size: 1.9375rem; line-height: 1.4em; }

h3 { font-size: 25px; font-size: 1.5625rem; line-height: 1.4em; }

h4 { font-size: 20px; font-size: 1.25rem; line-height: 1.4em; }

h5 { font-size: 16px; font-size: 1rem; line-height: 1.4em; }

h6 { font-size: 12px; font-size: 0.75rem; line-height: 1.4em; }

strong { font-weight: bold; }

.fonts-loaded strong { font-weight: normal; font-family: "hyundaisanshead-medium", Helvetica, Arial, sans-serif; }

/*------------------------------------ #TOP-LEVEL -------------------------------------*/
html { font-size: 16px; }

body { background-color: #ffffff; color: #000000; overflow-x: hidden; position: relative; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; font-family: "hyundaisanshead", Helvetica, Arial, sans-serif; font-weight: normal; font-size: 16px; font-size: 1rem; line-height: 24px; letter-spacing: normal; }

/* IE11 fix for images */
.iefix { opacity: 0; }

.object-fit { background-size: cover; background-position: center right; }

/* IE11 fix for images */
/*------------------------------------ #ACCORDION-OBJECT -------------------------------------*/
.o-accordion-header { display: flex; justify-content: space-between; font-family: "hyundaisanshead-medium", Helvetica, Arial, sans-serif; font-size: 18px; font-size: 1.125rem; line-height: 22px; cursor: pointer; padding: 15px 0; }

.o-accordion-panel { display: none; padding-bottom: 10px; }

.o-accordion-icon { background-color: #000000; border-radius: 50%; display: inline-block; height: 23px; width: 23px; position: relative; pointer-events: none; margin-left: 30px; }

.o-accordion-icon:before, .o-accordion-icon:after { background-color: #ffffff; content: ''; display: inline-block; height: 2px; left: 50%; position: absolute; top: calc(50% - 1px); transform: translateX(-50%); width: 10px; }

.o-accordion-icon:after { height: 10px; transform: translate(-50%, -45%) scaleY(1); transition: transform 0.4s cubic-bezier(0.55, 0, 0, 0.85); width: 2px; }

@media screen and (min-width: 768px) { .o-accordion-icon { display: none; } }

.o-accordion-icon--selected:after { transform: translate(-50%, -45%) scaleY(0); }

/*------------------------------------ #WRAPPER -------------------------------------*/
.o-wrapper { background-repeat: no-repeat, no-repeat; background-size: 80%, 80%; background-attachment: fixed; background-position: top 15% right -75%, top 65% left -75%; margin: 60px auto 0; overflow: hidden; position: relative; width: 100%; }

@media screen and (min-width: 768px) { .o-wrapper { background-position: top -25% right -75%, top 75% left -75%; } }

@media screen and (min-width: 1024px) { .o-wrapper { background-position: top -150% right -25%, top 300% left -25%; background-size: 65%, 65%; } }

@media screen and (min-width: 1200px) { .o-wrapper { background-position: top 300% right -60%, top -150% left -60%; background-size: 75%, 75%; } }

@media screen and (min-width: 1900px) { .o-wrapper { background-position: top 50% right -60%, top -150% left -60%; } }

.o-wrapper--purple { background-image: url("../images/rectangle-purple.svg"), url("../images/rectangle-purple.svg"); }

.o-wrapper--blue { background-image: url("../images/rectangle-blue.svg"), url("../images/rectangle-blue.svg"); }

.o-wrapper--yellow { background-image: url("../images/rectangle-yellow.svg"), url("../images/rectangle-yellow.svg"); }

.o-wrapper--green { background-image: url("../images/rectangle.svg"), url("../images/rectangle.svg"); }

body { background-color: green; }

/*------------------------------------ #SHOW-HIDE -------------------------------------*/
/** Hide only visually, but have it available for screen readers: https://snook.ca/archives/html_and_css/hiding-content-for-accessibility 1. For long content, line feeds are not interpreted as spaces and small width causes content to wrap 1 word per line: https://medium.com/@jessebeach/beware-smushed-off-screen-accessible-text-5952a4c2cbfe */
.u-visually-hidden { border: 0; clip: rect(0 0 0 0); height: 1px; margin: -1px; overflow: hidden; padding: 0; position: absolute; width: 1px; white-space: nowrap; /* [1] */ }
